@font-face {
  font-family: Sarabun;
  font-style: italic;
  font-weight: 400;
  src: local('Sarabun Italic'), local('Sarabun-Italic'), url(https://fonts.gstatic.com/s/sarabun/v6/DtVhJx26TKEr37c9aBB5k3wM.ttf) format('truetype');
}
@font-face {
  font-family: Sarabun;
  font-style: normal;
  font-weight: 300;
  src: local('Sarabun Light'), local('Sarabun-Light'), url(https://fonts.gstatic.com/s/sarabun/v6/DtVmJx26TKEr37c9YL5rilUs7g.ttf) format('truetype');
}
@font-face {
  font-family: Sarabun;
  font-style: normal;
  font-weight: 400;
  src: local('Sarabun Regular'), local('Sarabun-Regular'), url(https://fonts.gstatic.com/s/sarabun/v6/DtVjJx26TKEr37c9aBtJmg.ttf) format('truetype');
}
@font-face {
  font-family: Sarabun;
  font-style: normal;
  font-weight: 500;
  src: local('Sarabun Medium'), local('Sarabun-Medium'), url(https://fonts.gstatic.com/s/sarabun/v6/DtVmJx26TKEr37c9YOZqilUs7g.ttf) format('truetype');
}
@font-face {
  font-family: Sarabun;
  font-style: normal;
  font-weight: 600;
  src: local('Sarabun SemiBold'), local('Sarabun-SemiBold'), url(https://fonts.gstatic.com/s/sarabun/v6/DtVmJx26TKEr37c9YMptilUs7g.ttf) format('truetype');
}
@font-face {
  font-family: Sarabun;
  font-style: normal;
  font-weight: 700;
  src: local('Sarabun Bold'), local('Sarabun-Bold'), url(https://fonts.gstatic.com/s/sarabun/v6/DtVmJx26TKEr37c9YK5silUs7g.ttf) format('truetype');
}
@font-face {
  font-family: Sarabun;
  font-style: normal;
  font-weight: 800;
  src: local('Sarabun ExtraBold'), local('Sarabun-ExtraBold'), url(https://fonts.gstatic.com/s/sarabun/v6/DtVmJx26TKEr37c9YLJvilUs7g.ttf) format('truetype');
}
abbr,
address,
article,
aside,
audio,
b,
blockquote,
body,
canvas,
caption,
cite,
code,
dd,
del,
details,
dfn,
div,
dl,
dt,
em,
fieldset,
figcaption,
figure,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
header,
hgroup,
html,
i,
iframe,
img,
ins,
kbd,
label,
legend,
li,
mark,
menu,
nav,
object,
ol,
p,
pre,
q,
samp,
section,
small,
span,
strong,
sub,
summary,
sup,
table,
tbody,
td,
tfoot,
th,
thead,
time,
tr,
ul,
var,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  background: 0 0;
}
ins,
mark {
  background-color: #ff9;
  color: #000;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
nav ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:after,
blockquote:before,
q:after,
q:before {
  content: '';
  content: none;
}
a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: 0 0;
  text-decoration: none !important;
}
a:focus,
button:focus {
  border: 0;
  outline: 0;
}
ins {
  text-decoration: none;
}
mark {
  font-style: italic;
  font-weight: 700;
}
del {
  text-decoration: line-through;
}
abbr[title],
dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
.uneditable-input:focus,
input[type='color']:focus,
input[type='date']:focus,
input[type='datetime-local']:focus,
input[type='datetime']:focus,
input[type='email']:focus,
input[type='month']:focus,
input[type='number']:focus,
input[type='password']:focus,
input[type='search']:focus,
input[type='tel']:focus,
input[type='text']:focus,
input[type='time']:focus,
input[type='url']:focus,
input[type='week']:focus,
textarea:focus {
  border-color: var(--theme-bg-color) !important;
  outline: 0;
}
* {
  box-sizing: border-box;
}
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-track {
  background-color: #9d9d9d4b;
  cursor: pointer;
  border-radius: 4rem;
}
::-webkit-scrollbar-thumb {
  background-color: var(--theme-bg-color);
  cursor: pointer;
  border-radius: 4rem;
  width: 8px;
}
::selection {
  background-color: var(--theme-bg-color);
  color: var(--theme-tx-color);
}
.install-app-btn-container {
  display: none;
}
