:root {
  /* main colors */
  --main-color: #fff;
  /* --body-color: #f5f0f0; */
  --body-color: rgb(242, 249, 240);

  /* theme colors */
  --theme-bg-color: #000000;
  --theme-tx-color: #ffffff;
  --text-color: #000;

  /* setting */
  --border-radius: 0.4rem;
}

body,
html {
  width: 100%;
  height: 100vh;
  font-size: 14px;
  font-weight: 400;
  color: var(--text-color);
  font-family: Sarabun, sans-serif;
  -webkit-font-smoothing: subpixel-antialiased;
}

body {
  background-color: var(--body-color);
  zoom: 100%;
}

.container {
  width: 100%;
}

.navbar {
  width: 100%;
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
  background-color: var(--main-color);
}

.nav-menu ul {
  display: flex;
  align-items: center;
}

/* -----------------CartBarcodeMain----------------------- */
.CartBarcodeMain {
  width: 100%;
  padding: 1rem;
}
.nav-menu ul li a {
  margin-left: 1.4rem;
  color: var(--theme-bg-color);
}

.form-block {
  width: 100%;
  display: flex;
  align-items: center;
}

.form-block .input {
  margin-right: 1rem;
  display: flex;
  flex-direction: column;
}

.form-block .input input,
.form-block .input select,
.form-block .input button {
  display: block;
  height: 2rem;
  width: 12rem;
  padding: 0.2rem 0.4rem;
  border: none;
  border: 1px solid var(--body-color);
  outline: none;
  border-radius: var(--border-radius);
}
.form-block .action {
  display: flex;
  flex-direction: row;
}

.form-block .action button {
  width: 4rem;
  margin-right: 0.6rem;
  background-color: var(--theme-bg-color);
  color: var(--theme-tx-color);
}

.btn {
  background-position: center;
  -webkit-transition: background 0.1s;
  transition: background 0.1s;
}

.btn:hover {
  background: var(--main-color) radial-gradient(circle, transparent 1%, var(--main-color) 1%) center/15000%;
}

.btn:active {
  background-size: 100%;
  -webkit-transition: background 0s;
  transition: background 0s;
}

.barcode_preview_container {
  background-color: white;
  margin-top: 1rem;
  margin: 4px, 4px;
  padding: 0.4rem;
  /* border: 1px solid var(--theme-bg-color); */
  border-radius: var(--border-radius);
}
.barcode_preview {
  width: 100%;
  overflow-y: hidden;
  white-space: nowrap;
}

.barcode-logo {
  display: none;
}
@media print {
  @page {
    size: 40cm 28cm;
  }

  body {
    zoom: 100%;
    transform: scale(1);
    background-color: aqua;
  }

  svg {
    width: 100%;
    height: 80vmin;
    align-items: center;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    display: block;
  }

  .navbar {
    display: none;
  }
  .form-block {
    display: none;
  }
  .barcode-logo {
    font-size: 3rem;
    display: block;
  }
}
